.contact-container {
  background: url('/public/images/curved.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 7%;
}

.contact-us {
  background-color: rgba(255, 255, 255, 0.8);
  width: 80%;
  margin: auto;
}

.contact-us h1 {
  font-size: 10vw;
  color: #1055a5;
  padding-top: 8%;
  padding-left: 10%;
}

.new-way {
  font-size: 4vw;
  font-weight: 600;
  display: flex;
  justify-content: space-around;
}

.contact-para {
  width: 80%;
  margin: auto;
  font-size: 2.2vw;
}

.contact-cards {
  width: 80%;
  margin: auto;
  padding-bottom: 5vh;
  display: flex;
  flex-direction: column;
}

.contact-card {
  background-color: #1055a5;
  color: white;
  opacity: 1.5;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding: 2%;
  margin-bottom: 5%;
}

.contact-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.address {
  background-color: rgb(169, 245, 250);
  color: black;
  margin-top: 10%;
}

.contact-name {
  margin: 0;
  font-size: 2vw;
}

.contact-icon {
  font-size: 3vw;
  color: rgb(23, 131, 202);
  padding-right: 15px;
  margin-top: 4%;
}

.contact-details {
  font-size: 1.7vw;
  margin: 5px;
  color: white;
}
