.headline-outter-container {
  background-image: url("../../../public/images/games-server.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: calc(100vh - 75px);
  display: flex;
  justify-content: center;
}

.headline-inner-container {
  width: 80%;
  height: 50vh;
  background-color: rgba(46, 70, 146, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

.headline-inner-container img {
  width: 100%;
}

.headline-inner-container h1 {
  color: white;
  font-size: 9vw;
}

@media only screen and (min-width: 768px) {
  .headline-inner-container {
    height: 70vh;
  }

  .headline-inner-container img {
    width: 80%;
  }
}
