.team {
  background: rgb(247, 247, 247);
  box-sizing: border-box;
  padding: 20px 5%;
  margin: 0 5%;
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.team .member {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.team .member-img {
  width: 100%;
  overflow: hidden;
}

.team .member-img img {
  width: 100%;
  border-radius: 50%;
}

.team .member .name-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

.team .member .name-title span:first-child {
  font-weight: bold;
  font-size: 5vw;
}

.team .member .name-title span:nth-child(2) {
  font-size: 5vw;
}

@media only screen and (min-width: 768px) {
  .team .member {
    justify-content: flex-start;
    flex-direction: row;
    gap: 5%;
  }

  .team .member:nth-child(even) {
    align-self: flex-end;
    flex-direction: row-reverse;
    width: 100%;
  }

  .team .member-img {
    width: 40%;
  }

  .team .member .name-title span:first-child,
  .team .member .name-title span:nth-child(2) {
    font-size: 2vw;
  }
}
