.solution-outter-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.8);
  overflow-x: hidden !important;
}

.solution-title-container {
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0 200px;
}

.solution-title-container h2 {
  font-size: 8vw;
  margin: 0;
}

.solution-text-container-one {
  width: 90%;
  padding: 20px 20px;
  box-sizing: border-box;
  height: 90%;
  display: flex;
  align-items: center;
}

.override-one {
  margin-left: 150px;
}

.override-two {
  margin-left: 10px;
  background-color: rgba(233, 224, 224, 0.329);
  height: 100vh;
  width: 100%;
}

.solution-text-container-one .solution-subtitle {
  display: flex;
  margin-top: 10px;
  align-items: center;
  max-width: 90%;
}

.solution-text-container-one .subtitle {
  font-size: 3vh;
  margin-bottom: 20px;
  width: 60%;
}

.solution-text-container-one .solution-align-text {
  margin-left: 20px;
}

.icon-color {
  color: #389af6;
  padding-right: 20px;
}

.solution-text-container-one .solution-paragraph {
  font-size: 1.4vw;
  width: 85%;
}

.solution-text-container-one .container-two {
  margin-left: 40px;
}

@media only screen and (max-width: 1000px) {
  .solution-outter-container {
    background-image: url("../../assets/solution-img-two.PNG");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    color: black;
    max-height: 100%;
    overflow-x: hidden !important;
  }

  .solution-text-container-one .solution-paragraph {
    font-size: 4vw;
  }

  .override-one {
    display: block;
  }

  .override-two {
    display: block;
  }

  .override-two .container-two {
    margin-left: 100px;
  }

  .solution-text-container-one img {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .override-one {
    margin-left: 30px;
  }

  .override-two {
    margin-right: 100px;
  }
}

@media only screen and (max-width: 375px) {
  .solution-outter-container {
    max-height: 100%;
    overflow-x: hidden !important;
  }
}

@media only screen and (max-width: 280px) {
  .solution-outter-container {
    max-height: 100vh;
    overflow-x: hidden !important;
  }

  .override-one {
    margin-left: -40px;
    width: 100%;
  }

  .override-two {
    margin-right: 250px;
    width: 100%;
  }

  .override-two .solution-align-text {
    width: 300px;
  }

  .solution-title-container h2 {
    margin-left: -50px;
  }
}
