.keyfactors {
  margin: 20px 5%;
  padding: 10px 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: rgb(243, 243, 243);
}

.keyfactors h2 {
  font-size: 7.8vw;
  text-align: center;
}

.keyfactors-sections > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.keyfactors > div:nth-child(2) {
  display: flex;
  justify-content: center;
  margin-top: -10%;
}

.keyfactors img {
  width: 50%;
}

.keyfactors-sections {
  padding: 10px 5%;
}

.keyfactors-sections span {
  font-weight: bold;
}

.keyfactors-sections > div > div > span:first-child {
  background-color: rgb(165, 248, 244);
  padding: 2px;
}

.keyfactors-sections > div > div > span:nth-child(2) {
  margin-left: 5%;
}

hr {
  display: none;
}

@media only screen and (min-width: 768px) {
  .keyfactors h2 {
    font-size: 9vw;
  }

  hr {
    display: block;
    width: 100%;
  }

  .keyfactors-sections {
    display: flex;
    flex-wrap: wrap;
    gap: 5%;
  }

  .keyfactors-sections > div {
    width: 45%;
    align-items: flex-start;
  }
}
