header {
  width: 100%;
  height: 75px;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  box-sizing: border-box;
  background-color: white;
  border-bottom: 1px #cbcbcb solid;
}

.logo-img {
  width: 100%;
  height: 70px;
}

.hamburger-button {
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: white;
  box-sizing: border-box;
  padding: 75px 20px 0;
}

.menu-display {
  display: none;
}

.menu a,
.menu .drop-down-container {
  text-decoration: none;
  border-top: 1px solid black;
  padding: 10px;
}

.menu > a,
.menu .drop-down-icon {
  cursor: pointer;
  user-select: none;
  width: 100%;
  text-align: left;
}

.about-menu a,
.games-menu a {
  border-top: 1px solid black;
  padding: 10px;
  background-color: white;
}

.menu > a:last-child {
  border-bottom: 1px solid black;
}

.about-menu,
.games-menu {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-top: 10px;
  border: 0;
}

.hide-menu {
  height: 0;
  width: 0;
  overflow: hidden;
}

.close-icon {
  display: block;
  width: 30px;
  position: fixed;
  right: 10px;
  top: 20px;
  box-sizing: border-box;
}

.transparent-button {
  border: 0;
  width: fit-content;
  height: 30px;
  background: transparent;
  font-size: 15px;
}

.transparent-button img {
  width: 25px;
}

@media only screen and (min-width: 768px) {
  .hamburger-button {
    display: none;
  }

  .menu {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;
    position: unset;
    width: fit-content;
    height: 60px;
    padding: 0 10px;
    flex-direction: row;
  }

  .menu-display {
    display: flex;
  }

  .menu > a,
  .menu .drop-down-container {
    border-top: 0;
    padding: 0;
    width: fit-content;
  }

  .menu a:last-child {
    border-bottom: 0;
  }

  .about-menu,
  .games-menu {
    position: absolute;
    border: 1px solid black;
  }

  .close-icon {
    display: none;
  }
}

@media only screen and (min-width: 992px) {
  .logo-link {
    width: unset;
    margin-left: 5%;
  }
}
