.vission-mission-container {
  margin: 20px 5%;
  background-color: rgb(243, 243, 243);
  padding: 10px 5%;
  box-sizing: border-box;
}

.vision h2,
.mission h2 {
  font-size: 9vw;
  margin: 0;
  text-align: center;
}

.mission h2 {
  color: rgb(88, 148, 238);
  margin-bottom: 20px;
}

.mission span {
  width: 100%;
  display: flex;
  justify-content: center;
  font-weight: bold;
}

.mission > div:nth-child(2) span {
  background-color: rgb(88, 148, 238);
  color: white;
}

.mission > div:nth-child(3) span {
  background-color: rgb(165, 248, 244);
}

@media only screen and (min-width: 768px) {
  .vission-mission-container {
    background-image: url("../../../public/images/vission-and-mission-background.jpg");
    background-repeat: no-repeat;
    background-size: 25% 100%;
    background-position: right;
  }

  .vission-mission-container hr {
    width: 75%;
    margin-left: 0;
    margin-top: 50px;
  }

  .vision h2,
  .mission h2 {
    font-size: 7vw;
    text-align: left;
  }

  .vision p {
    width: 75%;
  }

  .mission > div {
    display: flex;
    width: 75%;
  }

  .mission > div > div {
    width: 50%;
  }

  .mission > div > div > p {
    width: 90%;
  }
}
