.market {
  background-color: rgb(169, 245, 250);
  display: flex;
  justify-content: center;
  padding-top: 8%;
}

.market-title {
  margin-bottom: -0.2%;
  width: 80vw;
}

.market-title h1 {
  font-size: 10vw;
  color: rgba(194, 246, 250, 0.959);
}

.company-info {
  background-color: hsl(0, 0%, 98%);
  margin: auto;
  width: 80%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.company-statistics {
  background-color: #1055a5;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 50px;
  padding: 25px;
  color: white;
}

.company-stat {
  display: flex;
  flex-direction: column;
}

.company-stat li {
  list-style-type: none;
  padding-top: 10px;
  font-weight: 300;
}

.company-description {
  padding: 10px;
  font-size: 1.8vw;
}

.figure {
  font-weight: 900 !important;
}

.competition-text h2 {
  font-size: 10vw;
  line-height: 14px;
}

.do-better {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 3.5vw;
  margin-bottom: -3vh;
}

.computer-icon {
  color: rgb(23, 131, 202);
  padding-right: 5px;
}

.computer-icon li {
  padding: 10px;
}

@media only screen and (min-width: 768px) {
  .company-description {
    font-size: 1.3vw;
  }
}
