.problem-outter-container {
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(209, 203, 203, 0.692);
}

.problem-title-container {
  background-color: rgba(255, 255, 255, 0.8);
  width: 90%;
  height: 45%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0 20px;
}

.problem-title-container h2 {
  font-size: 8vw;
  margin: 0;
}

.problem-text-container {
  width: 90%;
  height: 90%;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding: 0 20px;
}

.problem-text-container .problem-subtitle {
  display: flex;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  max-width: 85%;
}

.problem-text-container .subtitle {
  font-size: 2.5vh;
  margin-left: 10px;
}

.icon-color {
  color: #389af6;
}

.problem-text-container .problem-paragraph {
  font-size: 1.4vh;
  width: 85%;
}

.problem-text-container .list-container {
  width: 85%;
}

.problem-text-container .list-container .problem-list-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.problem-text-container .list-container .problem-list-container p {
  font-size: 1.5vh;
}

@media only screen and (min-width: 768px) {
  .problem-title-container h2 {
    font-size: 8vw;
    margin: 0;
  }

  .problem-text-container .problem-paragraph {
    font-size: 1.7vh;
  }

  .problem-text-container .list-container .problem-list-container {
    display: flex;
    flex-direction: row;
    gap: 10px;
  }

  .problem-text-container .list-container .problem-list-container p {
    font-size: 1.7vh;
  }
}

@media only screen and (min-width: 992px) {
  .problem-title-container h2 {
    font-size: 7vw;
    margin: 0;
  }

  .problem-text-container .subtitle {
    font-size: 1.8vw;
  }

  .about-text-container .problem-paragraph {
    font-size: 1vw;
  }
}
